e-height: 1.4;\n      font-weight: 400;\n      color: $about-us-subheader-text;\n  \n      margin-top: 16px;\n      margin-bottom: 12px;\n    }\n  }",".label-input-with-icon{\n    position: relative;\n  \n    display: flex;\n    flex-direction: column;\n  \n    width: 100%;\n    max-width: 524px;\n    \n    .label-icon{\n      position: absolute;\n      top: 56px;\n      left: 24px;\n      fill: #3C4942;\n      stroke: #3C4942;\n      z-index: 1;\n    }\n  \n    .input-with-icon{\n  \n      margin-top: 12px;\n  \n      outline: none;\n      border: none;\n      border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n      \n      color: $input-select-text;\n  \n      padding-top: 16px;\n      padding-bottom: 16px;\n      padding-left: 60px;\n      padding-right: 60px;\n  \n      &::placeholder{\n        color:$input-placeholder-text\n      }\n    }\n  \n}\n\n.label-input-with-icon.error .input-with-icon::placeholder{\n    color:$error-text;\n}\n.label-input-with-icon.error .error-icon{\n  opacity: 1;\n}\n.label-input-with-icon.error .error-message{\n  opacity: 1;\n}\n\n\n  \n.label-textarea{\n\n  display: flex;\n  flex-direction: column;\n\n  width: 100%;\n  max-width: 524px;\n\n  .textarea-field{\n    margin-top: 12px;\n\n    outline: none;\n    border: none;\n    border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n    \n    color: $input-select-text;\n\n    padding: 16px 24px;\n\n    position: relative;\n\n    resize: none;\n\n    &::placeholder{\n      color:$input-placeholder-text\n    }\n  }\n  \n}","header{\n    position: relative;\n    z-index: 11;\n    margin: 0 auto;\n}\n\n\n.header-backround{\n    background-color: $card-bg;\n    border-bottom-left-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n    border-bottom-right-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n\n    padding-top: 16px;\n    padding-bottom: 16px;\n\n    @include tablet{\n        padding-top: clamp(12px, 2.3vw, 32px);\n        padding-bottom: 0;\n        background-color: transpar