ard-bg;\n    width: 100%;\n\n    border-bottom-left-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n    border-bottom-right-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n\n    transition: $transition-burger;\n\n    overflow: hidden;\n    pointer-events: none;\n\n    @include tablet{\n        display: none;\n    }\n\n    &.burger-show{\n        padding-top: 32px;\n        padding-bottom: 32px;\n    \n        pointer-events: all;\n\n        gap: 24px;\n    }\n\n}\n\n.burger-item{\n    width: 40%;\n    opacity: 0;\n    transition: $transition-burger;\n\n    & a{\n        transition: $transition-burger;\n        max-height: 0px;\n    }\n\n}\n\n// .burger-item:nth-child(1){\n//     transition-delay: .500ms;\n// }\n// .burger-item:nth-child(2){\n//     transition-delay: .450ms;\n// }\n// .burger-item:nth-child(3){\n//     transition-delay: .400ms;\n// }\n// .burger-item:nth-child(4){\n//     transition-delay: .350ms;\n// }\n// .burger-item:nth-child(5){\n//     transition-delay: .300ms;\n// }\n\n.burger-item.item-show{\n    opacity: 1;\n    & a{\n        max-height: 40px;\n    }\n}",".modal-bgd{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    width: 100%;\n    height: 100%;\n    position: fixed;\n    top: 0;\n    left: 0;\n    background: $modal-bgd;\n\n    opacity: 0;\n    pointer-events: none;\n\n    transition: $transition;\n    \n\n    z-index: 11;\n}\n\n.pop-up.modal-active {\n    opacity: 1;\n    pointer-events: all;\n}\n\n\n.pop-up-form{\n    display: none;\n    opacity: 0;\n    // display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n\n    margin-left: 12px;\n    margin-right: 12px;\n\n    width: 100%;\n    max-width: 676px;\n\n    position: relative;\n\n    transition: $transition;\n    // transition-duration: 300ms;  //     ( \n    // transition-behavior: allow-discrete; //     ( \n\n    @media screen and (max-width: 389px){\n        gap: 30px;\n    }\n\n\n}\n.pop-up-form.show{